<template>
    <div id="page-wrap">
        <h1>Giỏ hàng</h1>
       <ProductsList :products="cartItems" 
       v-on:remove-from-cart="removeFromCart($event)"
       />
        <h3 id="total-price">Tổng: {{ totalPrice }}đ</h3>
        <button id="checkout-button">Thanh toán</button>
    </div>
</template>

<script>
import axios from 'axios';
import ProductsList from '@/components/ProductsList.vue';
    export default {
        name: 'CartPage',
        components: {
          ProductsList,
        },
        data(){
            return {
                cartItems: [],
            }
        },
        methods:{
          async removeFromCart(productId){
            const result = await axios.delete(`/api/users/123/cart/${productId}`);
            this.cartItems = result.data;
            
          }
        },
        computed: {
            totalPrice(){
                return this.cartItems.reduce(
                    (sum, item) => sum + Number(item.price),0,
                )
            }
        },
        async created(){
          const result = await axios.get('/api/users/123/cart');
          const cartItems = result.data;
          this.cartItems = cartItems;
        }
    };
</script>

<style scoped>
  h1 {
    border-bottom: 1px solid black;
    margin: 0;
    margin-top: 16px;
    padding: 16px;
  }

  #total-price {
    padding: 16px;
    text-align: right;
  }

  #checkout-button {
    width: 100%;
  }
</style>